import React, { Component } from "react";
import styled from "styled-components";
import { Layout as LayoutBase } from "@es";
import DetailTemp from "@demoComponents/DetailTemp";
import { Link } from "react-router-dom";
import CodeDemo from "@demoComponents/CodeDemo";
import ApiTable from "@demoComponents/ApiTable";

const Relate = styled.section``;
const Description = styled.section``;
const RouterLink = styled(Link)``;
const HashLink = styled.a``;
const Column = styled.div``;

const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table{
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;
class Layout extends Component {
  render() {
    const demoList = [
      {
        display: (
          <LayoutBase
            defaultStyles={`
          min-height: 0;
          &&>header{position: absolute; background: rgba(186,241,230,0.3)};
          &&>.wj-aside__left{
            height: 100px;
            background: rgba(155,243,225,0.6)
          }
        `}
            header={"header -- height: 64px"}
            asideLeft={"left -- width: 230px"}
            showLeft={true}
          >
            children
          </LayoutBase>
        ),
        description: <p></p>,
        code: `<LayoutBase
  defaultStyles={\`
  min-height: 0;
  &&>header{position: absolute; background: rgba(186,241,230,0.3)};
  &&>.wj-aside__left{
    height: 100px;
    background: rgba(155,243,225,0.6)
  }
\`}
    header={"header -- height: 64px"}
    asideLeft={"left -- width: 230px"}
    showLeft={true}
  >
    children
</LayoutBase>`
      },
      {
        display: (
          <LayoutBase
            defaultStyles={`
              min-height: 0;
              &&>header{position: absolute; background: rgba(186,241,230,0.3)};
              &&>.wj-aside__left{
                height: 100px;
                background: rgba(155,243,225,0.6);
              }
              &&>.wj-aside__right
              {
                background: rgba(155,243,225,0.6);
              }
              &&>footer{
                background: rgba(186,241,230,0.3);
              }
            `}
            header={"header"}
            asideLeft={"left"}
            asideRight={"right"}
            footer={"footer"}
            showLeft={true}
          >
            children
          </LayoutBase>
        ),
        description: <p></p>,
        code: `<Layout
    defaultStyles={\`
      min-height: 0;
      &&>header{position: absolute; background: rgba(186,241,230,0.3)};
      &&>.wj-aside__left{
        height: 100px;
        background: rgba(155,243,225,0.6);
      }
      &&>.wj-aside__right
      {
        background: rgba(155,243,225,0.6);
      }
      &&>footer{
        background: rgba(186,241,230,0.3);
      }
    \`}
    header={"header"}
    asideLeft={"left"}
    asideRight={"right"}
    footer={"footer"}
    showLeft={true}
  >
    children
  </Layout>`
      },
      {
        display: (
          <LayoutBase
            defaultStyles={`
          min-height: 0;
          &&>header{position: absolute; background: rgba(186,241,230,0.3)};
          &&>.wj-aside__left{
            height: 100px;
            background: rgba(155,243,225,0.6)
          }
        `}
            header={"header -- height: 64px"}
            asideLeft={"left -- width: 180px"}
            showLeft={true}
            leftSize="small"
          >
            children
          </LayoutBase>
        ),
        description: (
          <p>
             leftSize="small"
            navnav size="small"
          </p>
        ),
        code: `<LayoutBase
  defaultStyles={\`
  min-height: 0;
  &&>header{position: absolute; background: rgba(186,241,230,0.3)};
  &&>.wj-aside__left{
    height: 100px;
    background: rgba(155,243,225,0.6)
  }
\`}
    header={"header -- height: 64px"}
    asideLeft={"left -- width: 230px"}
    showLeft={true}
  >
    children
</LayoutBase>`
      }
    ];
    return (
      <DetailTemp title={"Layout"}>
        <Relate>
          
          <RouterLink to="./Header">Header</RouterLink>
          <RouterLink to="./Nav">Nav</RouterLink>
        </Relate>
        <Description>
          <h2></h2>
          <p>
            1. <br />
            2. <b>Layout</b>,<b>Nav</b>,<b>Header</b>
            3. <HashLink href="#api">api</HashLink>
          </p>
        </Description>
        <Column className={'demo-column'}>
          {demoList
            .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Column className={'demo-column'}>
          {demoList
            .filter((ele, index) => index % 2 === 1)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Api</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: " class",
                type: "string",
                default: ""
              },
              {
                name: "defaultStyles",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "children",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "showLeft",
                description: "",
                type: "bool",
                default: ""
              },
              {
                name: "leftSize",
                description:
                  ",  Nav ,  nav  size ",
                type: "string",
                default: ""
              }
            ]}
          />
        </Apis>
      </DetailTemp>
    );
  }
}

export default Layout;
