import React, { Component } from "react";
import styled from "styled-components";
import DetailTemp from "@demoComponents/DetailTemp";
import ApiTable from "@demoComponents/ApiTable";
import CodeDemo from "@demoComponents/CodeDemo";
import Demo1 from "./Demo1";

const Description = styled.section``;
const Column = styled.div``;

const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table {
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;

class Nav extends Component {
  render() {
    const demoList = [
      {
        display: <Demo1 />,
        description: "",
        code: `import React, { Component } from "react";
import { Nav } from "@es";
import { ReactComponent as Home } from "@es/static/medias/svg/home.svg";

class Demo1 extends Component {
  changeHandle = (id, data) => {
    alert('id:'+id )
  };
  render() {
    return (
      <Nav
        defaultActiveId="home1"
        onChange={this.changeHandle}
        navList={[
          {
            content: [<Home key={'home'} />, ""],
            id: "home1"
          },
          {
            content: "1",
            id: "home2",
            subList: [
              {
                content: <div></div>,
                id: "home21"
              },
              {
                content: <div>2</div>,
                id: "home22",
                subList: [
                  { content: <div>111</div>, id: "x1" },
                  { content: <div>222</div>, id: "x2" },
                  { content: <div>333</div>, id: "x3" }
                ]
              },
              {
                content: <div>2</div>,
                subViewType: "pop",
                id: "home23",
                subList: [
                  { content: <div>111</div>, id: "y1" },
                  { content: <div>222</div>, id: "y2" },
                  { content: <div>333</div>, id: "y3" }
                ]
              }
            ]
          }
        ]}
      />
    );
  }
}`
      },
    ];

    return (
      <DetailTemp title={"Nav"}>
        <Description>
          <h2></h2>
          <p>
            1. <br />
            2. layout  header
          </p>
        </Description>
        <Column className={'demo-column'}>
          {demoList
            .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Column className={'demo-column'}>
          {demoList
            .filter((ele, index) => index % 2 === 1)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Api</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: " class",
                type: "string",
                default: ""
              },
              {
                name: "defaultStyles",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "children",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "navList",
                description: "  ",
                type: "array",
                default: ""
              },
              {
                name: "size",
                description: "'small' ",
                type: "string",
                default: ""
              },
              {
                name: "onChange",
                description:
                  ", params: `(itemData.id, itemData)` //( id, )",
                type: "func",
                default: ""
              },
              {
                name: "activeId",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "defaultActiveId",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "noScroll",
                description:
                  ", ",
                type: "bool",
                default: ""
              }
            ]}
          />
        </Apis>
        <Apis>
          <h2>navList Api</h2>
          <ApiTable
            data={[
              {
                name: "content",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "id",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "subList",
                description: " `navList`",
                type: "array",
                default: ""
              },
              {
                name: "subViewType",
                description:
                  " : 'pop'  ",
                type: "string",
                default: ""
              },
              {
                name: "isOpen",
                description: "",
                type: "bool",
                default: ""
              },
              {
                name: "defaultIsOpen",
                description: "",
                type: "bool",
                default: ""
              },
              {
                name: "to",
                description: "",
                type: "string",
                default: ""
              }
            ]}
          />
        </Apis>
      </DetailTemp>
    );
  }
}

export default Nav;
