import React, { PureComponent } from "react";
import styled from "styled-components";
import DetailTemp from "@demoComponents/DetailTemp";
import ApiTable from "@demoComponents/ApiTable";
import CodeDemo from "@demoComponents/CodeDemo";
import Demo1 from "./Demo1";

const Description = styled.section``;
const Column = styled.div``;

const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table {
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;

class Tooltip extends PureComponent {
    render(){
        const demoList = [
            {
              display: <Demo1 />,
              description: "",
              code: `
              import React, { Component } from "react";
              import { Tooltip } from "@src";
              
              class Demo1 extends Component {
              
                  render() {
                    return (
                      <div>
                        <Tooltip title='' placement='up'></Tooltip>
                        <Tooltip title='' placement='down'></Tooltip>
                        <Tooltip title='' placement='left'></Tooltip>
                        <Tooltip title='' placement='right'></Tooltip>
                      </div>
                    );
                  }
                }
                
                export default Demo1;
            `
            },
          ];
        return(
       <DetailTemp title={"Tooltip"}>
        <Description>
          <h2></h2>
          <p>
            1. Tooltip
          </p>
        </Description>
        <Column className={'demo-column'}>
          {demoList
            .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Column className={'demo-column'}>
          {demoList
            .filter((ele, index) => index % 2 === 1)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Api</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: " class",
                type: "string",
                default: ""
              },
              {
                name: "defaultStyles",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "children",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "title",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "placement",
                description: "",
                type: "string",
                default: "up"
              }
            ]}
          />
        </Apis>

      </DetailTemp>
        )
    }
}



export default Tooltip;